$primaryColor: #224086;

@mixin ffrc() {
	font-family: 'Roboto Condensed'; }


%titleBorder {
	font-size: 60px;
	font-family: Cinzel;
	font-weight: 700;
	color: black;
	max-width: 480px;
	line-height: 1;
	text-transform: uppercase;
	@include r(992) {
		font-size: 48px;
		&:after {
			margin-top: 10px; } }
	@include r(640) {
		font-size: 36px; }
	&:after {
		content: '';
		height: 8px;
		background: #000;
		display: block;
		width: 30%;
		margin-left: -30px;
		margin-top: 20px;
		margin-bottom: 30px; } }

%mainText {
	@include ffrc;
	font-size: 16px;
	line-height: 20px;
	color: white;
	font-weight: 300;
	@include r(480) {
		font-size: 14px;
		line-height: 18px; } }



html,
body {
	height: 100%; }
body {
	position: relative; }

img {
	max-width: 100%; }

a {
	text-decoration: none; }

.text-right, {
	text-align: right; }

.text-center {
	text-align: center; }

.container {
	margin: 0 auto;
	max-width: 1180px;
	@include r(1180) {
		padding-left: 40px;
		padding-right: 40px; }
	&--wide {
		max-width: 1720px;
		@include r(1720) {
			padding-left: 10px;
			padding-right: 10px; } } }

.out {
	position: relative; }

button {
	background-color: transparent;
	appearance: none;
	outline: none;
	border: none; }

.site-header {
	transition: .5s;
	margin-bottom: 50px;
	position: relative;
	padding-top: 60px;
	@include r(768) {
		&__nav {
			a {
				color: black; }
			svg {
				path {
					fill: black; } }
			li {
				&:after {
					color: black; } } }
		&__menu {
			svg {
				fill: black; } } }
	.parallax & {
		padding-top: 3%;
		padding-bottom: 2%; }

	// .admin-bar .parallax &
	// 	padding-top: 15%
	// 	+r(1280)
	// 		padding-top: 23%
	.container {
		@include r(1080) {
			padding-left: 20px;
			padding-right: 20px; } }
	&__logo {
		@include r(640) {
			max-width: 40%;
			svg {
				max-width: 100%;
				min-width: 150px; } }
		@include r(480) {
			max-width: 30%; }
		&-img {
			max-height: 65px; } }
	.icon-search {
		svg {
			width: 14px;
			height: 14px; } }
	.modal-open & {
		opacity: 0;
		visibility: hidden; }
	&__top {
		display: flex;
		justify-content: space-between; }
	&__right {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__nav {
		@include r(420) {
			ul {
				flex-direction: column;
				li {
					margin-bottom: 5px;
					&:last-child {
						margin-left: 0; }
					&:after {
						content: none; } } } }
		ul {
			display: flex;
			justify-content: space-between; }
		li {
			&:last-child {
				margin-left: 10px;
				&:after {
					content: none; } }
			&:after {
				content: '/';
				color: white;
				display: inline-block;
				margin-left: 5px;
				margin-right: 8px; } }
		a {
			font-size: 14px;
			@include ffrc;
			color: white;
			text-transform: uppercase;
			font-weight: 300;
			&:hover {
				text-decoration: underline; } } }
	&__menu {
		margin-left: 30px;
		@include r(480) {
			margin-left: 15px; }
		svg {
			fill: white; } } }

.site-footer {
	min-height: 48px;
	background: #000;
	padding: 15px 0;
	.container {
		@include r(1080) {
			padding-left: 20px;
			padding-right: 20px; } }
	.parallax & {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0; }

	.copyright {
		font-size: 14px;
		@include ffrc;
		font-weight: 300;
		color: white;
		@include r(480) {
			margin-right: 10px; } }
	&__nav {
		display: flex;
		justify-content: space-between;
		nav {
			@include r(480) {
				display: flex;
				flex-direction: column;
				a {
					margin-bottom: 5px;
					&:after {
						content: none; } } }
			a {
				font-size: 13px;
				text-transform: uppercase;
				color: white;
				@include ffrc;
				font-weight: 300;
				transition: .5s;
				display: inline-block;
				&:hover {
					span {
						text-decoration: underline; } }
				&:last-child {
					&:after {
						content: none; } }
				&:after {
					content: '/';
					margin-left: 5px;
					margin-right: 8px; } } } }
	&__social {
		margin-left: 20px; } }

.row {
	display: flex;
	justify-content: space-between; }

.wrap {
	min-height: 100vh; }


.sidebar {
	transition: .5s;
	position: relative;
	@include r(768) {
		display: none; }
	&__title {
		font-size: 30px;
		width: 140px;
		line-height: 1;
		font-family: 'Cinzel';
		font-weight: 700;
		display: block;
		color: white;
		margin-left: 12px;
		margin-bottom: 15px;
		text-transform: uppercase;
		margin-bottom: 20px;
		&:after {
			content: '';
			height: 4px;
			width: 100%;
			display: block;
			background: white;
			margin-left: -12px;
			margin-top: 10px; } }
	.modal-open & {
		opacity: 0;
		visibility: hidden; }
	&__in {
		width: 280px;
		padding-bottom: 120px; }
	&__bg {
		background-color: #1D2B50;
		position: absolute;
		top: -100px;
		right: -1%;
		background: rgba(35, 39, 88, 0.9);
		width: 39%;
		height: 200%;
		transform: rotate(3deg);
		z-index: 0;
		@include r(768) {
			display: none; }
		@include r(900) {
			width: 43%; }
		@include r(850) {
			width: 47%; }
		@include rmin(2000) {
			width: 43%; }
		@include rmin(2560) {
			width: 45%; } }
	&__heading.news__heading {
		font-size: 30px;
		width: 140px; }
	&__icons {
		display: flex;
		align-items: center;
		margin-top: 20px;
		margin-left: 18px; }
	&__icon {
		margin-right: 20px;
		&--active {
			img {
				width: 44px; } } } }

.sidebar-menu {
	margin-top: 30px;
	border-bottom: 5px solid white;
	padding-bottom: 10px;
	&__item {
		padding-bottom: 10px;
		border-bottom: 1px solid white;
		&:last-child {
			border-bottom: 0; }
		&:hover,
		&--active {
			a {
				font-weight: 700 !important; } }
		& + & {
			margin-top: 10px; }
		a {
			transition: .2s;
			@include ffrc;
			text-transform: uppercase;
			font-weight: 300;
			font-size: 20px;
			line-height: 30px;
			color: white;
			text-decoration: none; } } }


.content {
	overflow: hidden;
	position: relative;
	&--dark {
		background: rgba(0,0,0,.5);
		height: 100vh;
		@include r(768) {
			&.content {
				height: auto;
				padding-bottom: 50px; } }
		.admin-bar & {
			height: calc(100vh - 50px); } }
	&__row {
		transition: .5s;
		.modal-open & {
			opacity: 0;
			visibility: hidden; } }
	&__bg {
		z-index: -1;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		.technopark4 & {
			top: -350px; }
		img {
			height: 100%;
			object-fit: cover;
			font-family: 'object-fit: cover';
			width: 100%;
			// .parallax &
 } }			// 	height: 60%

	&__title {
		@extend %titleBorder;
		max-width: 500px;
		color: white !important;
		text-shadow: 1px 1px 40px rgba(0,0,0,.7);
		&:after {
			background-color: white !important; } }
	&__sub {
		font-size: 24px;
		line-height: 30px;
		color: white;
		text-shadow: 1px 1px 40px rgba(0,0,0,.7); }

	&__texts {
		margin-top: 15%;
		color: white; }
	&__text {}
	.content__texts h1 {
		font-size: 60px;
		line-height: 1;
		font-family: Cinzel;
		font-weight: 700;
		color: white;
		text-transform: uppercase;
		@include r(768) {
			font-size: 36px; }
		&:first-child {
			text-indent: 50px;
			@include r(768) {
				text-indent: 30px; } }
		&:nth-child(2) {
			text-indent: 30px;
			@include r(768) {
				text-indent: 10px; } }
		&:last-child {
			text-indent: -30px;
			@include r(768) {
				text-indent: -10px; } } }

	&__links {
		margin-top: 250px; }

	&__links {
		display: flex;
		flex-grow: 1;
		align-items: flex-start; }
	&__link {
		cursor: pointer;
		flex-basis: 280px;
		& + & {
			margin-left: 20px; }
		&-icon {
			position: relative;
			border-bottom: 4px solid white;
			padding-bottom: 20px;
			max-width: 96px;
			margin-bottom: 20px;
			svg {
				filter: drop-shadow(0px 0px 20px #000); } }
		p {
			max-width: 230px;
			margin-top: 12px;
			margin-left: 15px; }
		a {
			font-size: 30px;
			font-family: 'Cinzel';
			font-weight: 700;
			line-height: 30px;
			color: white;
			text-shadow: 0 0 20px black;
			text-transform: uppercase;
			span {
				margin-left: 15px;
				display: block;
				max-width: 230px; } } } }




.news {
	$p: &;
	&__list {
		margin-bottom: 25px; }
	&__item {
		max-width: 280px;
		transition: .5s;
		padding-top: 10px;
		display: inline-block;
		@include rh(850) {
			&:last-child {
				display: none; } }
		&:hover {
			cursor: pointer;
			background-color: rgba(255, 255, 255, .8);
			& #{$p}__title,
			& #{$p}__text,
			& #{$p}__all,
			& #{$p}__category,
			& #{$p}__date,
			& #{$p}__more, {
				color: black; } }
		&:after {
			content: '';
			height: 1px;
			width: 100%;
			background: white;
			display: block;
			margin-top: 10px; } }
	&__in {
		padding-left: 20px;
		padding-right: 20px; }
	&__all {
		font-size: 20px;
		line-height: 16px;
		font-family: Cinzel;
		font-weight: 700;
		color: white;
		text-transform: uppercase;
		&:hover {
			text-decoration: underline; } }

	&__heading {
		@extend %titleBorder;
		color: white;
		margin-left: 18px;
		margin-bottom: 15px;
		&:after {
			content: '';
			height: 4px;
			width: 60%;
			display: block;
			background: white;
			margin-left: -18px;
			margin-bottom: 10px; } }
	&__top {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px; }
	&__category {
		color: white;
		@include ffrc;
		font-weight: 700;
		font-size: 10px;
		line-height: 24px; }
	&__date {
		font-size: 10px;
		color: white;
		line-height: 24px;
		font-weight: 300;
		@include ffrc; }
	&__title {
		font-size: 20px;
		color: white;
		@include ffrc;
		font-weight: 700;
		margin-bottom: 15px; }
	&__text {
		font-size: 14px;
		color: white;
		@include ffrc;
		font-weight: 300;
		line-height: 16px;
		margin-bottom: 15px; }
	&__more {
		font-size: 14px;
		font-family: Cinzel;
		font-weight: 700;
		color: white;
		margin-left: auto;
		text-align: right; } }


.page-content {
	min-width: 320px;
	max-width: 780px;
	margin-bottom: 150px;
	@include r(480) {
		margin-bottom: 250px; }
	&__block {
		margin-bottom: 60px;
		@include r(768) {
			.article__col4 {
				text-align: left;
				flex-basis: 25%; } } } }

.article {
	&:last-child {
		margin-bottom: 100px; }
	& + & {
		margin-top: 100px; }
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		margin-bottom: 80px;
		@include r(768) {
			flex-wrap: wrap;
			justify-content: space-around; }
		&--imgs180 {
			img {
				width: 180px;
				height: 180px;
				object-fit: cover;
				font-family: 'object-fit: cover';
				margin-bottom: 10px; } } }

	&__col {
		flex-basis: 50%;
		img {
			margin-bottom: 10px; }
		h2 {
			font-size: 37.5px;
			font-family: Cinzel;
			font-weight: 700;
			color: black;
			span {
				font-size: 24px;
				line-height: 30px;
				@include ffrc; }
			sup {
				vertical-align: super; } }
		p {
			font-size: 24px;
			line-height: 30px;
			@include ffrc;
			color: black;
			font-weight: 300; } }
	&__col33 {
		flex-basis: 33%;
		@include r(768) {
			flex-basis: 50%;
			flex-grow: 1;
			& + & {
				margin-top: 30px; } } }
	&__col4 {
		flex-basis: 25%;
		padding: 0 10px;
		@include r(768) {
			flex-basis: 50%;
			text-align: center;
			margin-bottom: 30px; } }

	&__table {
		&-title {
			font-size: 20px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 20px;
			text-transform: uppercase;
			@include ffrc; }
		table {
			border-top: 3px solid black;
			tr {
				&:last-child {
					border-top: 3px solid black;
					td {
						font-weight: 700; } } }


			td {
				font-size: 16px;
				font-weight: 700;
				line-height: 20px;
				color: black;
				padding: 10px 0;
				@include ffrc;
				span {
					font-weight: 300; }
				&:nth-child(2) {
					font-weight: 300; }
				&:last-child {
					text-align: right; } } } }
	&__timeline {
		position: relative;
		&:after {
			content: '';
			position: absolute;
			left: 90px;
			width: 5px;
			height: 99%;
			background-image: url(../img/circle.svg);
			background-repeat: no-repeat repeat;
			background-size: contain;
			top: 0px;
			display: inline-block;
			z-index: -1;
			@include r(768) {
				content: none; } }
		&-row {
			display: flex;
			margin-bottom: 70px;
			position: relative;
			@include r(768) {
				flex-direction: column;
				margin-bottom: 30px;
				border-bottom: 3px solid $primaryColor;
				padding-bottom: 30px; } }
		&-date {
			flex-basis: 180px;
			font-size: 37.5px;
			font-family: Cinzel;
			font-weight: 700;
			color: #383990;
			text-align: center;
			@include r(768) {
				font-size: 30px;
				flex-basis: auto;
				margin-bottom: 10px; }
			span {
				display: block;
				background: white; } }
		&-text {
			@include ffrc;
			font-size: 24px;
			font-weight: 300;
			line-height: 30px;
			color: black;
			@include r(768) {
				font-size: 20px;
				line-height: 26px; }
			p + p {
				margin-top: 20px; } } }

	&__title {
		@extend %titleBorder; }
	&__subtitle,
	&__h2 {
		@extend %titleBorder;
		font-size: 30px; }
	&__h2 {
		margin-left: auto;
		margin-right: auto;
		margin-top: 40px;
		margin-bottom: 20px;
		&:after {
			content: none; } }
	&__sub {
		margin-bottom: 100px;
		&-info {
			font-size: 16px;
			font-family: Cinzel;
			font-weight: 700; }
		&-top {
			margin-bottom: 50px; } }

	&__category, {
		@include ffrc;
		font-size: 20px;
		line-height: 24px;
		color: black;
		font-weight: 300;
		margin-bottom: 40px; }
	&__text {
		font-size: 24px;
		line-height: 30px;
		font-weight: 300;
		@include ffrc;
		color: black;
		margin-bottom: 40px;
		p + p {
			margin-top: 30px; } }
	&__more {
		font-size: 18px;
		font-family: Cinzel;
		font-weight: 700; }
	&__imgs {
		display: flex;
		justify-content: space-between;
		&-4 {
			.article__img {
				flex-basis: 20%; } }
		&-wrap {
			flex-wrap: wrap;
			.article__img {
				flex-grow: 0;
				& + .article__img {
					margin-left: 0; } } } }

	&__img {
		flex-basis: 48%;
		margin-bottom: 20px;
		flex-grow: 1;
		& + & {
			margin-left: 20px; } } }


.brand-slider {
	max-width: 620px;
	text-align: center;
	margin: auto;
	padding-left: 20px;
	padding-right: 20px;
	margin-top: 30px;

	.slick-arrow {
		position: absolute;
		top: 50%;
		margin-top: -26px;
		@include r(768) {
			margin-top: -46px;
			svg {
				width: 10px; } }
		&.slick-next {
			right: 0;
			transform: rotate(180deg); }
		&.slick-prev {
			left: 0; } }
	.slick-list {
		margin-right: -20px; }
	.slick-slide {
		width: 180px;
		margin-right: 20px;
		height: 100%;
		text-align: center; } }


.brand-slide {
	text-align: center;
	height: 85px;
	img {
		margin: auto;
		max-height: 100%;
		@include r(640) {
			height: 100%;
			object-fit: contain;
			font-family: 'object-fit: contain;'; } } }

.bg-slider {
	overflow: hidden;
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	z-index: -1;
	height: 100%;
	.bg-slide {
		width: 100%;
		height: 100%;
		img {
			height: 100%;
			width: 100vw;
			object-fit: cover;
			font-family: 'object-fit: cover'; } } }

.bg-nav {
	position: absolute;
	bottom: 100px;
	left: 50%;
	.slick-dots {
		display: flex;
		li {
			margin-right: 15px;
			&.slick-active {
				button {
					opacity: 1; } } }
		button {
			font-size: 0;
			width: 15px;
			height: 15px;
			background: white;
			opacity: .5;
			border-radius: 100%; } } }

.close-button {
	display: none; }

.modal {
	.content__links {
		margin-top: 200px; }
	&__logo {
		position: absolute;
		left: 80px;
		top: 60px;
		@include r(768) {
			left: 30px; }
		@include r(480) {
			max-width: 45%; }
		svg {
			max-width: 100%; }
		path,
		g {
			fill: white; } }
	.close {
		@include r(768) {
			right: 30px; } }
	&-bg {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		opacity: 0.1; }
	.site-footer {
		margin-top: 100px; } }

.login {
	&-modal {}
	.close-button {
		display: inline-block;
		color: white;
		font-size: 40px;
		line-height: 0; }
	.site-header__nav,
	.site-header__menu svg {
		display: none; }
	.content {
		min-height: 100vh;
		background: rgba(35,39,88,.9); } }

.login-form {
	max-width: 300px;
	margin: auto;
	&__icon {
		&:after {
			content: '';
			width: 79px;
			height: 4px;
			background: white;
			display: block;
			margin-top: 10px; } }
	&__text {
		font-size: 30px;
		line-height: 1;
		font-weight: 700;
		font-family: Cinzel;
		color: white;
		margin-left: 20px;
		margin-top: 10px;
		margin-bottom: 50px; }
	.form-group {
		&:nth-child(2),
		&:last-child {
			&:after {
				content: none; } }
		&:after {
			content: '';
			display: block;
			height: 1px;
			background: white;
			margin: 9px 0; } }
	input {
		line-height: 40px;
