.wpb_text_column {
	h1 {
		@extend %titleBorder; }
	h4 {
		@include ffrc;
		font-size: 20px;
		line-height: 24px;
		color: black;
		font-weight: 300;
		margin-bottom: 40px; }
	a {
		font-size: 16px;
		font-family: Cinzel;
		font-weight: 700;
		text-transform: uppercase; }
	p {
		font-size: 24px;
		line-height: 30px;
		font-weight: 300;
		@include ffrc;
		color: black;
		margin-bottom: 40px;
		& + & {
			margin-top: 30px; } }
	&.jobs__text {
		p,
		a {
			@include ffrc;
			font-size: 16px;
			line-height: 20px;
			font-weight: 300;
			margin-bottom: 0;
			color: black;
			text-transform: none; } } }


.page-content {
	.vc_row {
		margin-left: -10px;
		margin-right: -10px; }
	.vc_column_container {
		> .vc_column-inner {
			padding-left: 10px;
			padding-right: 10px; } }
	.vc_column_container {
		.vc_col-sm-6 {
			.wpb_single_image {
				img {
					height: 300px;
					object-fit: cover;
					font-family: 'object-fit: cover;'; } } } } }


.menu-home-container {
	margin-top: 250px;
	@include r(768) {
		margin-left: auto;
		margin-right: auto; } }

.home-menu {
	> .menu-item {
		& + .menu-item {
			margin-left: 20px; }
		> a {
			span {
				margin-left: 15px;
				display: block;
				max-width: 230px;
				.modal & {
					height: 60px;
					@include r(720) {
						height: 40px; } } } } } }

.home-menu,
.sidebar-menu {
	display: flex;
	flex-grow: 1;
	align-items: flex-start;
	@include r(768) {
		justify-content: center; }
	.modal & {
		justify-content: center; }
	.current-menu-parent {
		> a {
			font-size: 30px;
			width: 140px;
			line-height: 1;
			font-family: 'Cinzel';
			font-weight: 700;
			display: block;
			color: white;
			margin-left: 12px;
			margin-bottom: 15px;
			.modal & {
				&:after {
					content: none; } }
			&:after {
				content: '';
				height: 4px;
				width: 100%;
				display: block;
				background: white;
				margin-left: -12px;
				margin-top: 10px; }
			span {
				margin-left: 0; } } }
	.menu-item {
		cursor: pointer;
		flex-basis: 280px;
		@include r(992) {
			flex-basis: 140px;
			.modal & {
				flex-basis: 30%; } }
		&-icon {
			position: relative;
			border-bottom: 4px solid white;
			padding-bottom: 20px;
			max-width: 96px;
			margin-bottom: 20px;
			@include r(992) {
				max-width: 72px; }
			@include r(720) {
				max-width: 60px; }
			@include r(480) {
				max-width: 40px; }
			.modal & {
				height: 125px;
				@include r(992) {
					height: 100px; }
				@include r(720) {
					height: 70px; }
				@include r(480) {
					height: 50px; } }
			svg {
				filter: drop-shadow(0px 0px 20px #000); } }
		p {
			max-width: 230px;
			margin-top: 12px;
			margin-left: 15px; }
		a {
			font-size: 30px;
			font-family: 'Cinzel';
			font-weight: 700;
			line-height: 30px;
			color: white;
			text-shadow: 0 0 20px black;
			text-transform: uppercase;
			@include r(992) {
				font-size: 24px;
				line-height: 1; }
			@include r(720) {
				font-size: 20px; }
			@include r(480) {
				font-size: 18px; } } }
	.sub-menu {
		margin-top: 20px;
		@include r(480) {
			margin-top: 0; }
		li {
			border-bottom: 1px solid white;
			padding-bottom: 10px;
			padding-top: 10px;
			padding-left: 20px;
			padding-right: 20px;
			@include r(720) {
				padding: 10px; }
			&.current-menu-item {
				a {
					font-weight: 700; } }
			&:last-child {
				border-bottom: 0; } }
		a {
			font-size: 20px;
			font-weight: 300;
			@include ffrc;
			line-height: 24px;
			display: block;
			@include r(992) {
				font-size: 18px;
				line-height: 22px; }
			@include r(720) {
				font-size: 16px;
				line-height: 20px; }
			@include r(480) {
				font-size: 12px;
				line-height: 1.2; }
			&:hover {
				font-weight: 700; } } } }

.vc-icon-block {
	text-align: center;
	@include r(768) {
		margin-bottom: 20px; }
	img {
		max-width: 100px;
		height: auto;
		margin-bottom: 20px;
		height: 80px;
		object-fit: contain;
		font-family: 'object-fit: contain'; }
	&--small {
		.vc-icon-block__title {
			font-size: 16px;
			text-transform: uppercase;
			margin-bottom: 20px;
			@include ffrc;
			font-weight: 300;
			font-size: 16px; }
		.vc-icon-block__text {
			font-weight: 300;
			font-size: 16px; } }
	&__title {
		font-size: 37.5px;
		font-family: Cinzel;
		font-weight: 700;
		color: black;
		span {
			font-size: 24px;
			line-height: 30px;
			@include ffrc; }
		sup {
			vertical-align: super; } }
	&__text {
		font-size: 24px;
		line-height: 30px;
		@include ffrc;
		color: black;
		font-weight: 300; } }



.wpb_single_image {
	&.mh300 {
		img {
			height: 300px;
			object-fit: cover;
			font-family: 'object-fit: cover;';
			width: 100%; } }
	&.wh180 {
		img {
			@include rmin(768) {
				width: 180px;
				height: 180px;
				object-fit: cover;
				font-family: 'object-fit: cover;'; } } } }

.border-h2 {
	@extend %titleBorder;
	font-size: 30px; }

.vc_cta3 {
	background: transparent !important;
	border: none !important;
	h2 {
		font-family: Cinzel;
		font-weight: 700;
		color: #000 !important;
		max-width: 480px;
		line-height: 1;
		text-transform: uppercase;
		font-size: 30px;
		margin-left: auto !important;
		margin-right: auto !important; }
	.vc_btn3 {
		font-size: 16px;
		font-family: Cinzel;
		font-weight: 700;
		text-transform: uppercase;
		color: black !important;
		background: transparent !important;
		border: none !important; } }

.article-info__text {
	font-weight: 300;
	font-size: 16px;
	line-height: 20px;
	@include ffrc; }

.jobs__btn {
	.aio-icon-box-link {
		margin-left: auto; } }

.tab-pane {
	.row {
		justify-content: flex-start;
		flex-wrap: wrap;
		@include r(768) {
			justify-content: center; } }
	.project-item {
		margin-bottom: 20px;
		padding-left: 7px;
		padding-right: 7px; } }

.project-tabs {
	@include r(768) {
		.nav {
			justify-content: center; } }

	.nav-link {
		font-size: 16px;
		color: black;
		@include ffrc;
		font-weight: 300;
		text-transform: uppercase;
		padding-left: 12px;
		padding-right: 12px;
		&.active {
			font-weight: 700;
			background-color: transparent;
			color: black; } } }


.post-thumbnail {
	&--news {
		margin-bottom: 20px;
		img {
			max-height: 300px;
			width: 100%;
			object-fit: cover;
			font-family: 'object-fit: cover;'; } } }

.site-main {
	.site-header__nav {
		a {
			color: black;
			svg {
				path {
					fill: black; } } } }
	.site-header__menu {
		path {
			fill: black; } } }

.menu-modal-menu-container {
	.modal & {
		@include rh(800) {
			margin-top: 100px; } } }

